import React, { Component } from 'react'

export default class Homepage extends Component {

    constructor(props) {
        super(props)
    
        this.state = {

        }
    }
    
    render() {
        return (
            <div>
                <img src={this.props.pic} alt='pic' style={{width:'250px',height:'230px',borderRadius:'5px'}} ></img>
                <br/>
                <p style={{fontSize:'30px',fontFamily:'Times new roman',color:'navy'}}>{this.props.name}</p>  
                <p style={{fontSize:'30px',fontFamily:'Times new roman',color:'navy'}}>{this.props.price}$</p>
                <button type="button" class="btn btn-outline-success btn-lg" onClick={()=>{this.props.addtoCart(this.props.name,this.props.price,this.props.pic,this.props.count)}}>Add to cart</button>
                <hr/>
            </div>
        )
    }
}
