import React, { Component } from 'react'

export default class Cart extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
            index : props.index
        }
    }

    render() {
        return (
        <div style={{textAlign:'center'}}>          
            <img src={this.props.pic} alt='pic' style={{width:'250px',height:'230px',borderRadius:'5px'}} ></img>
            <br/>
            <p style={{fontSize:'30px',fontFamily:'Times new roman',color:'navy'}}>{this.props.name}</p>  
            <p style={{fontSize:'30px',fontFamily:'Times new roman',color:'navy'}}>{this.props.price}$ </p>
            <p style={{fontSize:'30px',fontFamily:'Times new roman',color:'navy'}}><button type="button" class="btn btn-outline-primary" onClick={()=>{this.props.countPlus(this.state.index)}} > + </button> {this.props.count} <button type="button" class="btn btn-outline-primary" onClick={()=>{this.props.countMin(this.state.index)}} > - </button> </p>
            <p style={{fontSize:'30px',fontFamily:'Times new roman',color:'navy'}}>total : {this.props.count * this.props.price }$</p>
            <button type="button" class="btn btn-outline-danger btn-lg" onClick={()=>{this.props.delProduct(this.props.index)}} > X </button>
        </div>     
        )
    }
}