import React, { Component } from 'react'
import 'bootstrap/dist/css/bootstrap.css'
import Homepage from './components/Homepage';
import a1 from './a1.jpg'
import a2 from './a2.png'
import a3 from './a3.jpg'
import a4 from './a4.jpg'
import Cart from './components/Cart';

export default class App extends Component {

  state = {
    products : [{name : 'iPhone 11 Pro 64GB' , price : 1200 , count : 1 , pic : a1 } ,
                {name : 'Galaxy S20 Ultra' , price : 1300 , count : 1 , pic : a2 } ,
                {name : 'AirPods Pro' , price : 200 , count : 1 , pic : a3  } ,
                {name : 'Galaxy Tab Active Pro' , price : 1000 , count : 1 , pic : a4 }],
    cart : [] ,
    flag:false,
  }

  addtoCart = (n,p,i,c) => { // add to cart
    this.setState({cart:[...this.state.cart,{name:n,price:p,pic:i,count:c}]})
    for(let j = 0;j<this.state.cart.length;j++){
      if(this.state.cart[j].name === n){
        this.state.cart[j].count = this.state.cart[j].count + 1
        this.setState({cart:[...this.state.cart]})
      }
    }
  }

  countPlus = (index) => { 
      this.state.cart[index].count = this.state.cart[index].count + 1
      this.setState({cart:[...this.state.cart]})
  }

  countMin = (index) => {
    this.state.cart[index].count = this.state.cart[index].count - 1
    this.setState({cart:[...this.state.cart]})
  }

  delProduct = (i)=>{ // remove from cart
    const delPro = this.state.cart.filter((element,index)=>(index!==i))
    this.setState({cart:[...delPro]})
  }

  buyCart = ()=>{ // Buy your cart
    this.setState({cart:this.state.cart.filter((element,index)=>(index!==index))})
    alert(`Your payment is ${this.totalPaid()}`)
  }

  totalPaid=()=>{ // total money you need to pay for the products in your cart
    let total = 0
    for(let i = 0 ; i<this.state.cart.length ; i++){
      total += this.state.cart[i].price * this.state.cart[i].count
    }
    return total
  }

  show = () =>{ 
    if(this.state.flag === true){
      return (
        <div>
          {this.state.cart.map((element,index)=>{
            return (
            <div style={{borderStyle:'groove',width:'300px',height:'550px',display:'inline-block',textAlign:'center'}}>
                 <Cart name={element.name} price={element.price} count={element.count} pic={element.pic} index={index} delProduct={this.delProduct} countPlus={this.countPlus} countMin={this.countMin}/>
              </div>
            )
            })} 
            <br/>  
            <br/>  
            <hr/>
            <h1 style={{fontFamily:'Times new roman',color:'navy'}}> Total is: {this.totalPaid()} $ </h1>
            <br></br>
            <button type="button" class="btn btn-outline-success btn-lg" style={{fontSize:'40px'}} onClick={this.buyCart} > BUY </button>
            <hr/> 
        </div>
      )
    }
    else{
      return (
        <div>
          {this.state.products.map((element,i)=>{
           return <Homepage name={element.name} price={element.price} count={element.count} pic={element.pic} index={i} addtoCart={this.addtoCart}/>
         })} 
        </div>
      )
    }
  }

  render() {
    return (
      <div style={{height:'120px',backgroundImage: 'linear-gradient(#df2673, #fe5031)',textAlign:'center'}}>
        <p style={{fontSize:'80px',fontFamily:'Times new roman',color:'white'}}> AmeerSH0P </p> 
        <button style={{fontSize:'50px',background:'none',border:'none',outline:'none'}} onClick={()=>{this.setState({flag:true})}}>🛒</button>
        <button style={{fontSize:'50px',background:'none',border:'none',outline:'none'}} onClick={()=>{this.setState({flag:false})}}>🏠</button>
        {this.show()}
      </div>
    )
  }
}